import Vue from 'vue'
const poolComponentModule = {
    state: {
        defaultPoolComponentUrl: '//127.0.0.1:9999/ajax/pool-component',
        current: 0,
        compList: []
    },
    mutations: {
        ChangePoolComponentUrl(state, payload) {
            state.defaultPoolComponentUrl = payload.url
        },
        SetPoolComponentCurrent(state, payload) {
            state.current = payload
        },
        SetPoolCompList(state, payload) {
            state.compList = payload
        }
    },
    // Action 提交的是 mutation，而不是直接变更状态。
    // Action 可以包含任意异步操作。
    actions: {
        InitPoolComponent({
            commit
        }, url) {
            Vue.http.post(url).then((response) => {
                let result = response.data
                result = (typeof result === 'string') ? JSON.parse(result) : result

                commit('SetPoolCompList', result.reduce((a, b) => {
                    return a.concat(b)
                }))

                commit('SetPoolElementList', result.reduce((a, b) => {
                    a[0].cls = "btn-danger f-cb";
                    b[0].cls = "btn-danger f-cb";
                    return a.concat(b)
                }))
            }, (response) => {
                console.log("组件池组件配置文件不存在！");
            }).catch(function (response) {
                console.log(response)
            })
        }
    },
    // 可以认为是 store 的计算属性
    getters: {
        getPoolComponentUrl: state => {
            return state.defaultPoolComponentUrl
        }
    }
}

export default poolComponentModule



