<template>
    <div class="project" @contextmenu="handleContextMenu" @click="clearSelect">
        <list :listData="projectList" :current="current" @contextmenu="handleContextMenu" @click="handleClick" @dblclick="handleDblclick"></list>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {
        mapState,
        mapGetters,
        mapActions,
        mapMutations
    } from 'vuex'
    import List from '@/components/List'
    import ContextMenu from '@/components/ContextMenu'
    import Modal from '@/components/Modal'
    export default {
        name: 'project-view',
        data() {
            return {}
        },
        computed: {
            ...mapState({
                current: state => state.project.current,
                projectList: state => state.project.projectList
            })
        },
        components: {
            List,
            ContextMenu,
            Modal
        },
        mounted() {
            this.$nextTick(function () { // 保证 this.$el 已经插入文档
                this.InitProjectList()
            })
        },
        methods: {
            handleContextMenu(e, item, index) {
                if (this.vm) {
                    this.vm.$el.innerHTML = ""
                    this.vm.$el.setAttribute("id", "contextMenu")
                }
                let _listData = [{
                    text: "新建",
                    type: "item",
                    handleClick: function () {
                        this.makeNewProjectItem()
                        this.vm.$el.innerHTML = ""
                        this.vm.$el.setAttribute("id", "contextMenu")
                    }.bind(this)
                }];
                if (item) {
                    _listData.push({
                        text: "编辑",
                        type: "item",
                        handleClick: function () {
                            this.makeNewProjectItem(item)
                            this.vm.$el.innerHTML = ""
                            this.vm.$el.setAttribute("id", "contextMenu")
                        }.bind(this)
                    });
                    _listData.push({
                        text: "删除",
                        type: "item",
                        handleClick: function () {
                            this.delProjectItem(index)
                            this.vm.$el.innerHTML = ""
                            this.vm.$el.setAttribute("id", "contextMenu")
                        }.bind(this)
                    });
                }
                _listData.push({
                    text: "保存",
                    type: "item",
                    handleClick: function () {
                        this.UpdateProject(JSON.stringify(this.projectList))
                        this.vm.$el.innerHTML = ""
                        this.vm.$el.setAttribute("id", "contextMenu")
                    }.bind(this)
                });
                this.vm = new Vue({
                    el: "#contextMenu",
                    render: page => page(ContextMenu, {
                        props: {
                            listData: _listData,
                            x: e.pageX,
                            y: e.pageY - 40
                        }
                    })
                })
                //阻止默认行为
                e.preventDefault();
                e.stopPropagation();
            },
            delProjectItem(index) {
                this.projectList.splice(index, 1)
                this.UpdateProject(JSON.stringify(this.projectList))
            },
            makeNewProjectItem(item) {
                var that = this;
                let vm = new Vue({
                    el: "#modalId",
                    render: page => page(Modal, {
                        props: {
                            title: item ? "编辑项目" : "创建项目",
                            okTxt: "确定",
                            cancelTxt: "取消",
                            listData: [{
                                label: "名称",
                                type: "input",
                                value: item && item.name || "",
                                key: "name",
                                tip: "项目名称，请使用英文字母"
                            }, {
                                label: "描述",
                                type: "input",
                                value: item && item.desc || "",
                                key: "desc",
                                tip: "简单介绍项目功能，每个项目是一个SPA或SP"
                            }]
                        },
                        on: {
                            cancel: function (data) {
                                vm.$el.innerHTML = ""
                                vm.$el.setAttribute("id", "modalId")
                            },
                            ok: function (data) {
                                // let name = data.find((it) => {
                                //     return it.key == "name"
                                // }).value;
                                if (item) {
                                    item = {
                                        "desc": data[1].value,
                                        "name": data[0].value,
                                        "thumbnail": ""
                                    }

                                } else {
                                    that.projectList.push({
                                        "desc": data[1].value,
                                        "name": data[0].value,
                                        "thumbnail": ""
                                    })
                                }
                                that.UpdateProject(JSON.stringify(that.projectList))
                                vm.$el.innerHTML = ""
                                vm.$el.setAttribute("id", "modalId")
                            }
                        }
                    })
                })
            },
            ...mapActions(['InitDataList']),
            handleDblclick(item, index) {
                this.SetProjectCurrent(index)
                this.GetPageModule(item)
                location.hash = '#/page/module'
            },
            handleClick(item, index) {
                this.SetProjectCurrent(index)
            },
            clearSelect() {
                if (this.vm) {
                    this.vm.$el.innerHTML = ""
                    this.vm.$el.setAttribute("id", "contextMenu")
                }
            },
            ...mapMutations(['InitProjectList']),
            ...mapMutations(['AddProject']),
            ...mapMutations(['UpdateProject']),
            ...mapMutations(['SetProjectCurrent']),
            ...mapMutations(['GetPageModule'])
        }
    }

</script>



