<template>
    <div id="pageList" @contextmenu="handleContextMenu">
        <list @contextmenu="handleContextMenu" :listData="pageList" :current="current" @click="handleClick" @dblclick="handleDblclick"></list>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {
        mapState,
        mapGetters,
        mapActions,
        mapMutations
    } from 'vuex'
    import ContextMenu from '@/components/ContextMenu'
    import Modal from '@/components/Modal'
    import List from '@/components/List'
    export default {
        name: 'tab-view',
        data() {
            return {
                vm: ""
            }
        },
        computed: {
            ...mapState({
                name: state => state.module.name,
                current: state => state.module.current,
                pageList: state => state.module.pageList
            }),
        },
        components: {
            List
        },
        mounted() {
            this.$nextTick(function () { // 保证 this.$el 已经插入文档
                let componentList = this.pageList.filter((e) => {
                    return e.type == "component"
                })
                this.InitPageComponent(componentList)

            })
        },
        methods: {
            handleContextMenu(e, item) {
                if (this.vm) {
                    this.vm.$el.innerHTML = ""
                    this.vm.$el.setAttribute("id", "contextMenu")
                }
                let _listData = [{
                    text: "新建",
                    type: "item",
                    handleClick: function () {
                        this.makeNewPageItem()
                        this.vm.$el.innerHTML = ""
                        this.vm.$el.setAttribute("id", "contextMenu")
                    }.bind(this)
                }];
                if (item) {
                    _listData.push({
                        text: "编辑",
                        type: "item",
                        handleClick: function () {
                            this.makeNewPageItem(item)
                            this.vm.$el.innerHTML = ""
                            this.vm.$el.setAttribute("id", "contextMenu")
                        }.bind(this)
                    });
                    _listData.push({
                        text: "删除",
                        type: "item",
                        handleClick: function () {
                            this.delPageItem(index)
                            this.vm.$el.innerHTML = ""
                            this.vm.$el.setAttribute("id", "contextMenu")
                        }.bind(this)
                    });
                }
                this.vm = new Vue({
                    el: "#contextMenu",
                    render: page => page(ContextMenu, {
                        props: {
                            listData: _listData,
                            x: e.pageX,
                            y: e.pageY - 40
                        }
                    })
                })
                //阻止默认行为
                e.preventDefault();
                e.stopPropagation();
            },
            delPageItem(index) {
                this.pageList.splice(index, 1)
                this.UpdatePage(JSON.stringify(this.pageList))
            },
            makeNewPageItem(item) {
                var that = this;
                let vm = new Vue({
                    el: "#modalId",
                    render: page => page(Modal, {
                        props: {
                            title: item ? "编辑页面或组件" : "创建页面或组件",
                            okTxt: "确定",
                            cancelTxt: "取消",
                            listData: [{
                                label: "名称",
                                type: "input",
                                value: item && item.name || "",
                                key: "name",
                                tip: "页面或组件名称，请使用英文字母"
                            }, {
                                label: "描述",
                                type: "input",
                                value: item && item.desc || "",
                                key: "desc",
                                tip: "简单介绍页面功能"
                            }, {
                                label: "类型",
                                type: "input",
                                value: item && item.type || "",
                                key: "type",
                                tip: "page或component，component可以被引用，page不行"
                            }]
                        },
                        on: {
                            cancel: function (data) {
                                vm.$el.innerHTML = ""
                                vm.$el.setAttribute("id", "modalId")
                            },
                            ok: function (data) {
                                // let name = data.find((it) => {
                                //     return it.key == "name"
                                // }).value;

                                if (item) {
                                    item = {
                                        "desc": data[1].value,
                                        "name": data[0].value,
                                        "type": data[2].value,
                                        "thumbnail": ""
                                    }
                                } else {
                                    that.pageList.push({
                                        "desc": data[1].value,
                                        "name": data[0].value,
                                        "type": data[2].value,
                                        "thumbnail": ""
                                    })
                                }
                                that.UpdatePage(JSON.stringify(that.pageList))
                                vm.$el.innerHTML = ""
                                vm.$el.setAttribute("id", "modalId")
                            }
                        }
                    })
                })
            },
            ...mapActions(['InitDataList']),
            ...mapActions(['SaveAs']),
            handleDblclick(item, index) {
                this.SetPageCurrent(index)
                this.GET2SetElementList({
                    name: this.name + "_" + item.name,
                    type: item.type
                })
                location.hash = '#/page/element'
            },
            handleClick(item, index) {
                if (this.vm) {
                    this.vm.$el.innerHTML = ""
                    this.vm.$el.setAttribute("id", "contextMenu")
                }
                this.SetPageCurrent(index)
            },
            ...mapMutations(['UpdatePage']),
            ...mapActions(['GET2SetElementList']),
            ...mapActions(['InitPageComponent']),
            ...mapMutations(['SetPageCurrent'])
        }
    }

</script>



