<template>
    <div id="compList">
        <list :listData="compList" :current="current" @click="handleClick" @dblclick="handleDblclick"></list>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {
        mapState,
        mapGetters,
        mapActions,
        mapMutations
    } from 'vuex'
    import List from '@/components/List'
    export default {
        name: 'poll-component-view',
        computed: {
            ...mapState({
                current: state => state.poolComponent.current,
                poolComponentUrl: state => state.poolComponent.defaultPoolComponentUrl,
                compList: state => state.poolComponent.compList
            })
        },
        components: {
            List
        },
        mounted() {
            if (!this.compList.length) {
                this.$nextTick(function () { // 保证 this.$el 已经插入文档
                    this.initData()
                })
            }
        },
        methods: {
            initData() {
                this.poolComponentUrl && this.InitPoolComponent(this.poolComponentUrl)
            },
            ...mapActions(['InitPoolComponent']),
            handleClick(item, index) {
                this.SetPoolComponentCurrent(index)
            },
            handleDblclick(item, index) {
                this.SetPoolComponentCurrent(index)
            },
            ...mapMutations(['SetPoolComponentCurrent']),
        }
    }

</script>



