<template>
    <div id="pageList">
        <list :listData="pageList" :current="current" @click="handleClick" @dblclick="handleDblclick"></list>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {
        mapState,
        mapGetters,
        mapActions,
        mapMutations
    } from 'vuex'
    import List from '@/components/List'
    export default {
        name: 'pool-module-view',
        computed: {
            ...mapGetters({
                pageList: 'getListData',
                poolModuleUrl: 'getPoolModuleUrl'
            }),
            ...mapState({
                current: state => state.poolModule.current
            })
        },
        components: {
            List
        },
        mounted() {
            this.$nextTick(function () { // 保证 this.$el 已经插入文档
                this.initPageData()
            })
        },
        methods: {
            initPageData() {
                this.poolModuleUrl && this.InitDataList(this.poolModuleUrl)
            },
            ...mapActions(['InitDataList']),

            handleDblclick(item, index) {
                this.SetPoolModuleCurrent(index)
            },
            handleClick(item, index) {
                this.SetPoolModuleCurrent(index)
            },
            ...mapMutations(['SetPoolModuleCurrent'])
        }
    }

</script>



