let localstorage = window.localStorage;
export default {
    get: function (key) {
        return localstorage.getItem(key);
    },
    del: function (key) {
        return localstorage.removeItem(key);
    },
    set: function (key, value) {
        return localstorage.setItem(key, value);
    }
}



